import styled from "styled-components";

export const StyledButton = styled("button")<any>`
  background: ${(p) => p.color || "linear-gradient(90deg, #006400, #228B22)"};
  color: ${(p) => (p.color ? "#00ff00" : "#ffffff")};
  font-size: 1.2rem;
  font-weight: 700;
  width: 100%;
  border: 1px solid #edf3f5;
  border-radius: 4px;
  padding: 13px 0;
  cursor: pointer;
  margin-top: 0.625rem;
  max-width: 180px;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 16px 30px rgb(23 31 114 / 20%);

  &:hover,
  &:active,
  &:focus {
    color: #fff;
    border: 1px solid #006400;
    background: linear-gradient(90deg, #228B22, #006400);
    box-shadow: 0 16px 30px linear-gradient(90deg, #004d00, #008000);
  }
`;
